.theme-switcher {
  cursor: pointer;
  transition: all .3s ease-out;
  display: inline-block;
}

.theme-switcher__slider {
  background: #cecdcd;
  border-radius: 60px;
  width: 46px;
  height: 20px;
  transition: background-color .3s ease-out, box-shadow .3s ease-out;
  display: block;
  position: relative;
  box-shadow: 7px 5px 15px #a09ea099;
}

.theme-switcher__slider:before {
  content: "";
  background: #f8f8f8;
  border-radius: 18px;
  width: 18px;
  height: 18px;
  transition: all .3s ease-out;
  position: absolute;
  top: 1px;
  left: 1px;
}

.theme-switcher__input:checked + .theme-switcher__slider {
  background: #9bb537;
}

.theme-switcher__input:checked + .theme-switcher__slider:before {
  left: calc(100% - 1px);
  transform: translate(-100%);
}

.theme-switcher__input:checked + .theme-switcher__slider {
  box-shadow: 7px 5px 15px #16161699;
}

.theme-switcher__slider:active:before {
  width: 25px;
}

.theme-switcher__input:focus-visible + .theme-switcher__slider {
  outline-offset: 2px;
  border-radius: 50px;
  outline: 2px solid #1976d2;
}

